/* ---base rules--- */
button,
input,
select,
a {
  font-family: "Nunito Sans", sans-serif;
  color: inherit;
}

a {
  text-decoration: none;
}

div svg {
  max-inline-size: 100%;
}
div img {
  max-inline-size: 100%;
}

ul {
  list-style-type: none;
  padding-left: 0;
}

/* ---layout--- */
.header,
.main {
  padding: 0 5vw;
}

/* ---modules---- */
.header {
  display: flex;
  justify-content: space-between;
  align-items: center;
}
.header__theme__switch {
  display: flex;
  justify-content: space-between;
  align-items: center;
  min-width: 110px;
}
.header__theme__icon {
  max-width: 20px;
}

.countriesSearchPage section {
  margin-top: 30px;
}

.countriesSearchPage .search {
  margin-top: 6px;
}

.search__icon {
  height: 24px;
  position: relative;
  top: 38px;
  left: 28px;
}
.search__input {
  box-sizing: border-box;
  width: 100%;
  height: 45px;
  padding: 0 10px 0 80px;
  border: none;
  border-radius: 5px;
  font-size: inherit;
}

.regionMenu select {
  width: 100%;
  padding: 0 28px;
  height: 45px;
  border: none;
  border-radius: 5px;
  font-size: inherit;
}

.countriesList {
  display: grid;
  max-width: 1380px;
  margin: auto;
  grid-template-columns: repeat(auto-fill, minmax(300px, 1fr));
  justify-items: center;
  gap: 60px;
}
.countriesList__countryGeogrpahy {
  padding: 20px;
}
.countriesList__countryGeogrpahy li:first-child {
  padding-bottom: 10px;
  font-size: large;
}

.countryItem {
  border-radius: 5px;
}
.countryItem img {
  border-radius: 5px 5px 0 0;
}

.countryPage__backToSearch {
  display: inline-block;
  padding: 10px;
  margin: 30px 0;
  border-radius: 5px;
}

.countryPage__data {
  line-height: 1.8;
  font-weight: 300;
}
.countryPage__data ul {
  margin-bottom: 40px;
}
.countryPage__data span {
  font-weight: 600;
}
.countryPage__data p {
  font-weight: 600;
}

.countryPage_bordersList {
  display: flex;
  flex-flow: row wrap;
}
.countryPage_bordersList li {
  background-color: hsl(209deg, 23%, 22%);
  padding: 7px;
  border-radius: 3px;
  margin: 10px;
}

/* ---theme--- */
html {
  background-color: hsl(207deg, 26%, 17%);
  color: white;
}

select,
input,
button,
.countryItem {
  background-color: hsl(209deg, 23%, 22%);
}

.header {
  background-color: hsl(209deg, 23%, 22%);
}
.header__theme__switch {
  border: none;
}
.header__theme__icon {
  fill: white;
}

.search__icon {
  fill: white;
}

.countryPage__backToSearch {
  background-color: hsl(209deg, 23%, 22%);
}

